/* VerifyEmail.module.css */

.verifyEmailContainer {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.verifyEmailContent {
  margin-bottom: 20px;
}

.verifyEmailContent h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.verifyEmailContent p {
  font-size: 16px;
  line-height: 1.6;
  color: #333;
}

.verifyEmailButton {
  padding: 12px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background-color 0.3s ease;
}

.verifyEmailButton:hover {
  background-color: #0056b3;
}
