.userProfile {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.profileInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.profileImage {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 10px;
  border: 2px solid #ddd;
}

.noProfileImage {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ddd;
  color: #666;
  font-size: 20px;
  border: 2px solid #ccc;
}

.uploadProgress {
  margin-top: 10px;
  font-size: 16px;
  color: #333;
}

.profileInput,
.profileTextarea {
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.profileActions {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.saveButton,
.cancelButton,
.editButton,
.deleteButton {
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.saveButton {
  background-color: #4caf50;
  color: white;
}

.saveButton:hover {
  background-color: #45a049;
}

.cancelButton {
  background-color: #f44336;
  color: white;
}

.cancelButton:hover {
  background-color: #d32f2f;
}

.editButton {
  background-color: #2196f3;
  color: white;
}

.editButton:hover {
  background-color: #1976d2;
}

.deleteButton {
  background-color: #f44336;
  color: white;
}

.deleteButton:hover {
  background-color: #e53935;
}

.hidden {
  display: none;
}

.fileInput {
  display: none;
}

.fileInputLabel {
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  background-color: #ff9800;
  color: white;
  transition: background-color 0.3s ease;
  margin-top: 20px; /* Add some space between the file input and the profile picture */
}

.fileInputLabel:hover {
  background-color: #fb8c00;
}

.verifiedBadge {
  display: inline-block;
  padding: 5px 10px;
  background-color: #4caf50;
  color: white;
  font-size: 14px;
  border-radius: 4px;
  margin-top: 10px;
}

.notVerifiedBadge {
  display: inline-block;
  padding: 5px 10px;
  background-color: #f44336;
  color: white;
  font-size: 14px;
  border-radius: 4px;
  margin-top: 10px;
}
