.signInContainer {
  max-width: 400px;
  margin: 100px auto;
  padding: 30px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.notificationBanner {
  background-color: #ffd700;
  color: #333;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.signInTitle {
  font-size: 28px;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
}

.googleButton {
  position: relative; /* Position relative for shading effect */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  background-color: #fff;
  color: #444;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, box-shadow 0.3s ease; /* Added box-shadow transition */
}

.googleButton:hover {
  background-color: #f1f1f1;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Added subtle box-shadow on hover */
}

.googleButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.googleLogo {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.buttonText {
  font-size: 16px;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
}
