/* Dashboard.module.css */

.dashboardContainer {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.actions {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.addButton {
  padding: 12px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background-color 0.3s ease;
  margin-top: 16px;
}

.addButton:hover {
  background-color: #0056b3;
}

.errorContainer {
  background-color: #ffcccc;
  color: #d8000c;
  padding: 10px;
  border: 1px solid #ba2b2b;
  border-radius: 4px;
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.errorContainer p {
  margin: 0;
  flex: 1;
}

.closeButton {
  background-color: transparent;
  border: none;
  color: #d8000c;
  cursor: pointer;
  font-size: 16px;
}

.closeButton:hover {
  color: #9b0000;
}

.checkInList {
  margin-top: 20px;
}

.checkIn {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
}

.checkIn h3 {
  font-size: 18px;
  margin-bottom: 12px;
}

.checkIn p {
  font-size: 16px;
  margin-bottom: 8px;
}

.checkIn .actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;
}

.checkIn .actions button {
  padding: 10px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  margin-left: 8px;
  transition: background-color 0.3s ease;
}

.checkIn .actions button:hover {
  background-color: #0056b3;
}

.modalBackground {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContainer {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 600px;
}

.verifyEmailContainer {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.verifyEmailContainer p {
  margin-bottom: 16px;
}

.resendButton {
  padding: 12px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.resendButton:hover {
  background-color: #0056b3;
}
