.messagingPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f5f5f5; /* Set background color for the messaging page */
  min-height: 100vh; /* Ensure full height */
}

.pageTitle {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333; /* Dark color for the title */
  text-align: center;
}

.messageContainer {
  display: flex;
  width: 100%;
  max-width: 800px;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  background-color: white; /* White background for message container */
}

.recipientsList {
  flex: 1;
  max-width: 250px;
  border-right: 1px solid #ccc;
  padding: 10px;
}

.recipientsList h3 {
  margin-bottom: 10px;
}

.searchInput {
  width: calc(100% - 20px); /* Adjusted width to account for padding */
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.recipientsList ul {
  list-style: none;
  padding: 0;
}

.recipientsList li {
  padding: 8px;
  cursor: pointer;
  border-radius: 4px;
  margin-bottom: 5px;
}

.recipientsList li:hover,
.selectedRecipient {
  background-color: #007bff;
  color: white;
}

.conversation {
  flex: 2;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.conversation h3 {
  margin-bottom: 10px;
}

.messageList {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
  background-color: #e5e5e5;
  border-radius: 8px;
  margin-bottom: 10px;
}

.message {
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
  max-width: 60%;
  word-wrap: break-word;
}

.sentMessage {
  background-color: #007bff;
  color: white;
  align-self: flex-end;
  text-align: left; /* Ensure text aligns to the left */
  margin-left: auto;
  border-bottom-right-radius: 0;
}

.receivedMessage {
  background-color: #f1f1f1;
  align-self: flex-start;
  text-align: left;
  margin-right: auto;
  border-bottom-left-radius: 0;
}

.messageForm {
  display: flex;
}

.messageInput {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
}

.sendButton {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.sendButton:hover {
  background-color: #0056b3;
}

.notificationBubble {
  background-color: #007bff;
  color: white;
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 10px;
  width: 100%;
  text-align: center;
}
