.checkInForm {
  width: 90%; /* Adjust width as needed */
  max-width: 800px; /* Maximum width */
  margin: 0 auto;
  padding: 30px; /* Increased padding for more space */
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.checkInForm h3 {
  font-size: 1.8rem;
  margin-bottom: 15px;
  color: #333;
}

.formGroup {
  margin-bottom: 20px;
}

.label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  color: #555;
}

.textArea {
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: vertical;
}

.textArea:focus {
  outline: none;
  border-color: #66afe9;
}

.buttons {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.submitButton, .cancelButton {
  padding: 12px 20px;
  background-color: #5cb85c;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  margin-left: 8px;
  transition: background-color 0.3s ease;
}

.submitButton:hover {
  background-color: #4cae4c;
}

.error {
  color: #a94442;
  background-color: #f2dede;
  border: 1px solid #ebccd1;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
}

.error p {
  margin: 0;
  flex: 1;
}

.closeButton {
  background-color: transparent;
  border: none;
  color: #721c24;
  cursor: pointer;
}

.closeButton:hover {
  color: #d9534f;
}
